// SearchResults.js
import React from "react";
import CodingQuestionCard from "./CodingQuestionCard";
import { Grid } from "@mui/material";

const mystyle = {
  listStyleType: "none",
  padding: "7%",
};

function SearchResults({ results }) {
  return (
    <div>
      <ul style={mystyle}>
        <Grid container spacing={{ xs: 2, md: 3 }}>
          {results.map((result) => (
            <Grid item xs={2} sm={4} md={4} key={result._id}>
              <CodingQuestionCard
                id={result._id}
                questionName={result.problem_name}
                companyNames={result.tags.company_tags}
                topicNames={result.tags.topic_tags}
              />
            </Grid>
          ))}
        </Grid>
      </ul>
    </div>
  );
}

export default SearchResults;
