import React, { useEffect, useState } from 'react'
import { useParams } from 'react-router-dom'
import SolutionParser from '../components/SolutionParser/SolutionParser'
import './CodingQuestion.css'

const CodingQuestion = () => {
  const [data, setData] = useState({
    problem_name: '',
    problem_description: '',
    problem_solution: '',
    tags: { topic_tags: '' },
  })
  const [loading, setLoading] = useState(false)
  const [error, setError] = useState(null)
  const { questionId } = useParams()
  const baseUrl = '/api/LEET/question'
  const apiUrl = `${baseUrl}/${questionId}`

  useEffect(() => {
    // Function to fetch question data
    const fetchQuestionData = async () => {
      setLoading(true)
      try {
        const response = await fetch(apiUrl)
        if (!response.ok) {
          throw new Error('Network response was not ok')
        }
        const result = await response.json()
        setData(result)
      } catch (error) {
        setError(error.message)
      } finally {
        setLoading(false)
      }
    }

    fetchQuestionData()
  }, [apiUrl])
  if (loading) {
    return <div>Loading...</div>
  }
  if (error) {
    return <div>Error: {error}</div>
  }

  return (
    <div className='hero'>
      <div className='logoBar'>
        <img
          src='https://cantileverlabs-quiz-answer-images.s3.ap-south-1.amazonaws.com/1644050775219_CLLogo.png'
          alt='cantilever-logo'
        />
      </div>
      <div className='container'>
        <div className='contentBox'>
          <strong>Problem Name:</strong> {data.problem_name}
        </div>
        <div className='contentBox'>
          <strong>Topic Tags: </strong>
          {data.tags && Array.isArray(data.tags.topic_tags)
            ? data.tags.topic_tags.join(', ')
            : 'No topic tags available'}
        </div>
        <div className='contentBox'>
          <strong>Problem Description:</strong>
          <div
            dangerouslySetInnerHTML={{
              __html: data.problem_description.replace(/\n/g, '<br>'),
            }}
          />
        </div>
        <div className='contentBox'>
          <SolutionParser data={data.problem_solution} />
        </div>
      </div>
    </div>
  )
}

export default CodingQuestion
