import React from 'react'
import { useState } from 'react'
import './SolutionParser.css'

const parseContent = data => {
  // Regular expression to match sections
  const solutionRegex =
    /Approach[\s\S]*?(?=Approach|Text content of the <iframe>|$)/g
  const iframeRegex =
    /Text content of the <iframe>[\s\S]*?(?=Approach|Text content of the <iframe>|$)/g

  // Extract content based on regex
  const solutionApproaches = [...data.matchAll(solutionRegex)].map(match =>
    match[0].trim(),
  )
  const iframeContents = [...data.matchAll(iframeRegex)]
    .map(match => match[0].trim())
    .filter(content => content.length >= 60)
    .map(content => content.replace('Text content of the <iframe>:', ''))

  return { solutionApproaches, iframeContents }
}

const SolutionParser = ({ data }) => {
  const { solutionApproaches, iframeContents } = parseContent(data)
  const [selectedApproach, setSelectedApproach] = useState(0)
  const [selectedContent, setSelectedContent] = useState(0)

  if (data === '') return <div></div>

  return (
    <div className='main'>
      <div className='column'>
        <h2>Solution Approaches</h2>
        {solutionApproaches.map((approach, index) => (
          <button
            key={`button-${index}`}
            onClick={() => setSelectedApproach(index)}
          >
            Approach {index + 1}
          </button>
        ))}

        {selectedApproach !== null && (
          <div>
            <h3>Approach {selectedApproach + 1}</h3>
            <div
              dangerouslySetInnerHTML={{
                __html: solutionApproaches[selectedApproach],
              }}
            />
          </div>
        )}
      </div>

      <div className='column'>
        <h2>Code Answer</h2>
        {iframeContents.map((content, index) => (
          <button
            key={`button-content-${index}`}
            onClick={() => setSelectedContent(index)}
          >
            Answer {index + 1}
          </button>
        ))}

        {selectedContent !== null && (
          <div>
            <h3>Answer {selectedContent + 1}</h3>
            <pre>{iframeContents[selectedContent]}</pre>
          </div>
        )}
      </div>
    </div>
  )
}

export default SolutionParser
