// App.js
import React, { useState, useEffect } from "react";
import SearchBar from "./components/SearchBar";
import SearchResults from "./components/SearchResults";
import axios from "axios";
import styles from "./styles/App.module.css";
// import { useParams } from "react-router-dom";
import { useQuery } from "@tanstack/react-query";

function App() {
  // const { companyName } = useParams();
  const [companyName, setCompanyName] = useState(null);

  const { data, isLoading, isFetched } = useQuery({
    queryKey: ["search-companies", companyName],
    queryFn: () =>
      axios.get("api/LEET/search", {
        params: {
          companyTags: companyName || "",
        },
      }),
    enabled: !!companyName,
  });

  console.log(data);

  useEffect(() => {console.log(companyName)}, [companyName])

  return (
    <div className={styles.app}>
      <div className={styles.logoBar}>
        <img
          src="https://cantileverlabs-quiz-answer-images.s3.ap-south-1.amazonaws.com/1644050775219_CLLogo.png"
          alt="cantilever-logo"
          className={styles.pageLogo}
        />
      </div>
      <SearchBar onSearch={(e) => setCompanyName(e)} />
      {isLoading && (
        <div className={styles.pageOverview}>
          <span>Loading please wait...</span>
        </div>
      )}
      {isFetched && !isLoading && (
        <>
          <div className={styles.pageOverview}>
            We have prepared a list of <span>{data.data.length}</span> questions
            to practise for your next {companyName} Interview !
          </div>
          <SearchResults results={data.data} />
        </>
      )}
    </div>
  )
}

export default App
