import { Card, CardContent, Chip, CardHeader, Fab } from "@mui/material";
import ArrowForwardIcon from "@mui/icons-material/ArrowForward";
import styles from "./CodingQuestionCard.module.css";
import { useNavigate } from "react-router-dom";

export default function CodingQuestionCard({
  questionName = "",
  companyNames = [],
  topicNames = [],
  id
}) {
  const navigate = useNavigate();

  function handleCardClick(){
    navigate("/coding-question/" + id);
  }
  return (
    <Card
      sx={{
        // minWidth: '30rem',
        // maxWidth: '100%',
        border: "0.5px solid #0077b6",
        margin: "2%",
      }}
      className="coding-question-card"
      // onClick={() => setClicked((prev) => !prev)}
    >
      <CardHeader
        sx={{
          ".MuiCardHeader-content": {
            width: "100%",
          },
        }}
        title={<div className={styles.cardHeader}>{questionName}</div>}
      ></CardHeader>
      <CardContent>
        <div style={{ display: "flex", gap: "3.5px" }}>
          {topicNames.map((val, index) => (
            <Chip label={val} />
          ))}
        </div>
        <div
          style={{
            display: "flex",
            justifyContent: "flex-end",
          }}
        >
          <Fab color="primary" aria-label="add" onClick={() => handleCardClick()}>
            <ArrowForwardIcon />
          </Fab>
        </div>
      </CardContent>
    </Card>
  );
}
