// SearchBar.js
import React, { useState, useEffect } from "react";
import Select from "react-select";
import { Button } from "@mui/material";
import styles from "../styles/App.module.css";

function SearchBar({ onSearch }) {
  const [companies, setCompanies] = useState([]);
  const baseUrl = "api/LEET/companies";
  const [selectedOptions, setSelectedOptions] = useState(null);

  useEffect(() => {
    // Fetch data from the API and update the state variable
    async function fetchData() {
      try {
        const response = await fetch(baseUrl); // Replace with your API endpoint
        if (!response.ok) {
          throw new Error("Network response was not ok");
        }

        const jsonData = await response.json();
        setCompanies(
          jsonData.map((company) => ({ label: company, value: company }))
        ); // Update the state with the fetched data
      } catch (error) {
        console.error("Fetch error:", error);
      }
    }

    fetchData(); // Call the fetchData function when the component mounts
  }, []);

  const handleSubmit = (e) => {
    e.preventDefault();
    const selectedValues = selectedOptions.value;
    onSearch(selectedValues);
  };

  return (
    <form
      style={{
        margin: "0 7%",
        display: "flex",
        gap: "7px",
      }}
    >
      <Select
        options={companies}
        value={selectedOptions}
        onChange={setSelectedOptions}
        className={styles.searchBarCompanies}
      />
      <Button onClick={handleSubmit} variant="contained">Search</Button>
    </form>
  );
}

export default SearchBar;
